import React, { Fragment } from "react";
import { Hidden } from "@material-ui/core";

import MobileWallet from "../../../Container/AllPayments/Wallet/Mobile";
import DesktopWallet from "../../../Container/AllPayments/Wallet/Desktop";

import Layout from "../Layout";

interface IWalletPage {}

const WalletPage: React.FunctionComponent<IWalletPage> = () => {
  return (
    <Fragment>
      <Hidden smUp implementation="js">
        <MobileWallet />
      </Hidden>
      <Hidden xsDown implementation="js" only="xs">
        <Layout useFluid>
          <DesktopWallet />
        </Layout>
      </Hidden>
    </Fragment>
  );
};

export default WalletPage;
